import React, { useEffect, useRef, useState } from 'react'
import { MessageList } from 'react-chat-elements'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faComment } from '@fortawesome/free-solid-svg-icons'
import 'react-chat-elements/dist/main.css';
import useChatState from '../chat/use-chat-state'
import { Tooltip } from 'reactstrap'

const Chat = ({ onSearchResults }) => {

  const { dataSource, processInput, isTyping, sessionAttributes } = useChatState();
  const [isChatTipOpen, setChatTipOpen] = useState(false);
  const [numberOfResults, setNumberofResults] = useState(0);

  const inputEl = useRef(null);
  const messageList = useRef(null);

  const onKeyPress = (e) => {
    if (e.shiftKey && e.charCode === 13) {
      return true;
    }
    if (e.charCode === 13) {
      onUserMessage();
      e.preventDefault();
      return false;
    }
  };

  const onUserMessage = () => {
    processInput(inputEl.current.value);
    inputEl.current.value = '';
  }

  useEffect(() => {
    const node = document.querySelector('#is-typing');
    if (node) {
      node.remove();
    }
    if (isTyping) {
      const existingNode = document.querySelector('#is-typing');
      if (!existingNode) {
        const newNode = document.createElement('div');
        newNode.setAttribute('id', 'is-typing')
        newNode.setAttribute('class', 'loading');
        messageList.current.mlistRef.appendChild(newNode);
      }
    }
  });

  useEffect(() => {
    if (!sessionAttributes.searchResults) {
      return;
    }

    onSearchResults(sessionAttributes.searchResults.results);
    setNumberofResults(sessionAttributes.searchResults.number);
  }, [sessionAttributes.searchResults])

  return (
    <>
      <div className="chat">
        <MessageList
          ref={messageList}
          className="message-list h-100"
          lockable={ true }
          toBottomHeight="100%"
          dataSource={ dataSource }
        />
      </div>

      <div className="form-group">
        <div className="input-group mt-5">
          <input ref={ inputEl } onKeyPress={onKeyPress} type="text" className="form-control" placeholder="Do a search or ask Patty..." aria-label="Do a search or ask Patty" aria-describedby="basic-addon2"/>
            <div className="input-group-append">
              <button title="Ask Patty" onClick={onUserMessage} className="btn" type="button" id="chatBtn"><FontAwesomeIcon icon={ faComment } /></button>
              <Tooltip delay={{show: 0, hide: 0}} placement="bottom" isOpen={isChatTipOpen} target="chatBtn" toggle={() => setChatTipOpen(!isChatTipOpen)}>
                Chat with Patty
              </Tooltip>
            </div>
        </div>
        { numberOfResults > 0 && <small className="form-text light-primary-colour">{`${ numberOfResults } ${ numberOfResults !== 1 ? 'results' : 'result'} found (so far)`}</small>}
      </div>
    </>
  );
};

export default Chat;