import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import Pick from './pick'

const Picks = () => {
  return (
    <StaticQuery
      query={graphql`
    {
      categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
            edges {
                node {
                    frontmatter {
                        tag
                        name
                        icon
                    }
                    fields {
                        slug
                    }
                }
            }
        }
      picks: allMarkdownRemark(filter: {frontmatter: {pick: {eq: true}}}) {
        edges {
          node {
            fields {
              slug
            }
            frontmatter {
              title
              description
              image
              tags
            }
            html
          }
        }
      }
    }
    `}
      render={data => {

        return (
          <div className="container-fluid">
            <div className="row mt-5 pb-5 picks">
              <div className="col-md">
                <div className="row">
                  <div className="col-md">
                    <h2 className="title--header small">Patty's Picks</h2>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12">
                    <div className="card-columns">
                      {
                        data.picks.edges.map(edge => edge.node).map((node, idx) => {
                          const listingIcons = node.frontmatter.tags
                              .map(tg => data.categories.edges.map(edge => edge.node).find(catNode => catNode.frontmatter.tag === tg))
                              .filter(catNode => catNode)
                              .map(catNode => ({
                                icon: catNode.frontmatter.icon,
                                name: catNode.frontmatter.name,
                                path: catNode.fields.slug
                              }));
                          return (
                            <Pick
                              key={ idx }
                              title={ node.frontmatter.title }
                              url={ node.fields.slug }
                              icons={ listingIcons }
                              description={ node.frontmatter.description }
                              content={ node.html }
                              image={ node.frontmatter.image }/>
                          );
                        })
                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )
      }}
    />
  )
};

export default Picks;