import React from 'react'
import moment from 'moment'

const BlogSummary = ({ title, excerpt, image, url, author, date }) => (
  <a className="link light" href={url}>
    <div className="card bg-transparent mb-5">
      {image && <img src={image} alt={title} className="card-img-top"/>}
      <div className="card-body">
        <h4 className="card-title">{title}</h4>
        <p><small>
            <time className="light-primary-colour pt-2" dateTime={moment(date)} itemProp="datePublished">
              {moment(date).format('dddd, D MMMM YYYY')}
            </time>
            <span itemProp="author" itemScope itemType="http://schema.org/Person">  • <span
              className="light-primary-colour" itemProp="name">{author}</span>
                      </span>
          </small>
        </p>
        <div className="card-text primary-text-colour" dangerouslySetInnerHTML={{ __html: excerpt }}/>
      </div>
    </div>
  </a>
);

export default BlogSummary;