import React, { useEffect, useRef } from 'react'
import { graphql, StaticQuery } from 'gatsby'
import SearchResult from './search-result'

const Search = ({ results }) => {

  const searchContainerRef = useRef(null);

  useEffect(() => {
    searchContainerRef.current.scrollTop = 0;
  }, [results]);

  return (
    <StaticQuery
  query={graphql`
      {
        categories: allMarkdownRemark(filter: {frontmatter: {type: {eq: "category"}}}) {
              edges {
                  node {
                      frontmatter {
                          tag
                          name
                          icon
                      }
                      fields {
                          slug
                      }
                  }
              }
          }
        }
    `}
  render={
    data => {
      return (
        <>
          <div className="search" ref={ searchContainerRef }>
            {
              results ? results.map((h, idx) => {
                const listingIcons = h.tags ? h.tags
                  .map(tg => data.categories.edges.map(edge => edge.node).find(catNode => catNode.frontmatter.tag === tg))
                  .filter(catNode => catNode)
                  .map(catNode => ({
                    icon: catNode.frontmatter.icon,
                    name: catNode.frontmatter.name,
                    path: catNode.fields.slug
                  })) : [];
                return (
                    <SearchResult key={ idx } description={ h.description } icons={ listingIcons } url={ h.url } title={ h.title }/>
                );
              }) : (
                <div className="row text-center align-items-center h-100">
                  <div className="col-md">
                    <h2 className="title--header small mb-5">Some things you can search or ask me:</h2>
                    <p><em>"How do I do my Spanish residency?"</em></p>
                    <p><em>"things to do near Fuengirola"</em></p>
                    <p><em>"Can I receive healthcare in Spain?"</em></p>
                    <p><em>"windsurfing"</em></p>
                    <p><em>"softplay in Malaga"</em></p>
                  </div>
                </div>
              )
            }
          </div>
        </>
    )
    }}
  />
  )
};

export default Search;