import React from 'react';
import CategoryIcon from './category-icon'

const SearchResult = ({ title, description, icons, url }) => {
  return (
    <div className="row mb-3">
      <div className="col-md">
        <h1 className="title--header small"><a className="link light" href={ url } target="_blank" rel="noopener noreferrer">{ title }</a></h1>
        <p><small>{ description }</small></p>
        <p>{
          icons && icons.map((ic, iconIndex) => (
            <CategoryIcon icon={ ic.icon } name={ ic.name } path={ ic.path } key={ iconIndex }/>
          ))
        }</p>
      </div>
    </div>
  );
};

export default SearchResult;