import React from 'react';
import { graphql, StaticQuery } from 'gatsby'
import BlogSummary from './blog-summary'
import moment from 'moment';

const BlogList = () => {
  return (
    <StaticQuery
      query={graphql`
      {
        bloglist: allMarkdownRemark(sort: {fields: frontmatter___date, order: DESC},
            filter: {frontmatter: {type: {eq: "blog"}}}, limit: 10) {
          edges {
            node {
              fields {
                slug
              }
              excerpt(pruneLength: 500, format: HTML)
              frontmatter {
                title
                image
                date
                author
              }
            }
          }
        }
      }
      `}
      render={data => {
        const now = moment();
        return (
          <div className="container-fluid blog">
            <h2 className="title--header small">Patty's Blog</h2>
            {
              data.bloglist.edges.filter(({ node }) => now.isAfter(node.frontmatter.date)).map(({ node }, idx) => (
                <BlogSummary
                  key={ idx }
                  excerpt={ node.excerpt }
                  image={ node.frontmatter.image }
                  url={ node.fields.slug }
                  title={ node.frontmatter.title }
                  author={ node.frontmatter.author }
                  date={ node.frontmatter.date }/>
              ))
            }
          </div>
        );
      }} />
  );
};

export default BlogList;