import React from 'react';
import CategoryIcon from './category-icon'

const Pick = ({ title, description, image, url, icons, content }) => {
  return (
    <div className="card">
      {image && (
        <a href={ url }>
          <img src={ image.replace('x400', 'x800') } alt={ title } className="card-img-top" />
        </a>)
        }
      <div className="card-body">
        <h3 className="card-title">
          <a className="link" href={ url }>
            { title }
          </a>
        </h3>
        <div className="card-text dark-colour" dangerouslySetInnerHTML={{__html: content}} />
        <p>
          {
            icons && icons.map((ic, idx) => (
              <CategoryIcon icon={ic.icon} name={ic.name} path={ic.path} key={idx}/>
            ))
          }
        </p>
      </div>
    </div>
  );
};

export default Pick;